import { runInAction, makeAutoObservable } from "mobx";
import OBSWebSocket from "obs-websocket-js";

type IScenes = {
    sceneIndex: number;
    sceneName: string;
}

class Scenes {
    status = "disconnected";

    obs = new OBSWebSocket();
    scenes: IScenes[] = [];
    selectedScene: string = "";

    url = "";
    password = "";

    constructor() {
        makeAutoObservable(this);
    }

    init() {
        this.obs.connect(this.url, this.password)
            .then((info) => runInAction(() => this.status = "connected" ),
                () => runInAction(() => { this.status = "disconnected" }));
            this.obs.on('Identified', () => {
                this.obs.call('GetSceneList').then((data) => runInAction(() => this.scenes = data.scenes as IScenes[]));
                this.obs.call('GetCurrentProgramScene').then((data) => runInAction(() => this.selectedScene = data.currentProgramSceneName));
            });
            this.obs.on('CurrentProgramSceneChanged', data => {
                runInAction(() => {
                    this.selectedScene = data.sceneName;
                });
            });
    }

    changeScene(sceneName: string) {
        this.obs.call('SetCurrentProgramScene', { sceneName: sceneName });
    }
}

export default new Scenes();