import React from 'react';
import OBS from './store/OBS';
import { observer } from 'mobx-react';

import './App.css';

const App: React.FC = observer(() => {
  function changeScene(state: React.MouseEvent<HTMLButtonElement>) {
    const btn = state.target as HTMLButtonElement;
    OBS.changeScene(btn.innerText);
  }

  function connect() {
    const url = document.getElementById('url') as HTMLInputElement;
    const password = document.getElementById('password') as HTMLInputElement;

    OBS.url = url.value;
    OBS.password = password.value;
    OBS.init();
  }

  return (
    <div className="App">
      <h1 style={{ color: OBS.status === 'connected' ? 'lime' : 'red' }}>{OBS.status}</h1>
      <div>
        <p className='obsData-text'>WebSocket URL:&nbsp;&nbsp;&nbsp;<input className='obsData' id='url' type='text' value='ws://192.168.1.10:4455'/></p>
        <p className='obsData-text'>WebSocket password:&nbsp;&nbsp;&nbsp;<input className='obsData' id='password' type='password' /></p>

        <button className='connectBtn' onClick={connect}>Connect</button>
      </div>
      <br /><br />
      {OBS.scenes.map(item => <button className={OBS.selectedScene === item.sceneName ? 'selectedSceneBtn' : 'sceneBtn'} key={item.sceneIndex} onClick={changeScene}>{item.sceneName}</button>)}
    </div>
  );
});

export default App;
